___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --dark-red: #540b0e;
    --red: #9e2a2b;
    --light-yellow: #e09f3e;
    --green-blue: #335c67;

    --bg-colour: rgb(22, 22, 22);
    --inner-bg-colour: rgb(46, 46, 46);
    --header-height: 60px;
    --small-header: 30px;
    --text: 25px;

    --card-board-radius: 25px;
    --card-text: 24px;
    --card-width: 400px;
    --content-padding: 25px clamp(20px, 10vw, 500px);
}

@font-face {
    font-family: Louis;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
    font-family: lumina;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

body {
    margin: 0px;
    background-color: var(--bg-colour);
    font-family: Louis;
}

header {
    display: flex;
    color: white;
    background-color: var(--bg-colour);
    border-bottom: 1px solid #3a3a3a;
    height: var(--header-height);

    & h1 {
        padding-left: 20px;
        flex: auto;
        display: inline;
        margin: 0px;
        line-height: var(--header-height);
    }
}

nav {
    flex: 3 1 auto; 
    display: flex;
    justify-content: end;
}

button {
    color: white;
    font-size: 22px;
    border: 0px;
    background-color: rgba(0,0,0,0);
    font-family: Louis;
    width: 100px
}

button:hover {
    color: black;
    background-color: var(--light-yellow);
}

.bg-video-container {
    height: 70vh;
    position: absolute;
    top: 0;
    overflow: hidden;
    width: 100%;
    z-index: -1;    
}

#bg-video {
    filter: opacity(0.45);
}

@media screen and (max-width: 1560px) {
    #bg-video {
        height: 100%;
    }
}

@media screen and (min-width: 1560px) {
    #bg-video {
        width: 100%;
    }
}

.important-title {
    font-family: lumina;
    font-style: italic;
    letter-spacing: 3px;
}

.page-title {
    font-size: 120px;
    color: white;
    text-align: center;
}

.title-sub-text {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-style: normal;
}

.video-bg-btn-container {
    display: flex;
    gap: 15px;
    justify-content: center;
}

.video-bg-btn {
    border: 2px solid white;
    padding: 20px;
    width: 200px;
}

.hero-text-container {
    text-align: center;
    color: #f1f1f1;
    width: 100%;
    padding-top: 100px;
    font-style: italic;
    
    
    & h1 {
        margin-bottom: 0px;
    }

    & h3 {
        margin-top: 10px;
        font-size: 50px;
    }
}

.home-page-content-container {
    background-color: var(--bg-colour);
    margin-top: 250px;
    margin-bottom: 100px;
    padding: var(--content-padding);
    color: white;
}

.text-container {
    --small-header: 30px;

    text-align: center;
    font-size: var(--small-header);
    border: 2px solid white;
    padding: 50px 40px;
    line-height: calc(var(--small-header) + 10px);
    margin: 70px 0px 100px 0px;
    color: white;
}

.home-page-content-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 3fr 1fr;
    column-gap: 30px;
    row-gap: 30px;
}

.hours-container,
.address-container,
.reservation-container,
.contact-container {
    background-color: var(--inner-bg-colour);
    font-size: var(--text);
    padding: 30px;
    text-align: center;

    & h2 {
        font-size: var(--small-header);
    }
}

.hours-container {
    grid-column: 2/3;
    grid-row: 1/2; 
    display: flex;
    flex-direction: column;
    align-items: center;
}

table {
    flex: auto;
    padding-bottom: 10px;
    border-spacing: 30px;
    display: flex;

    & tr {
        flex: auto;
    }
}

.address-container {
    grid-column: 2/4;
    grid-row: 2/3;
}

.reservation-container {
    grid-column: 1/2;
    grid-row: 1/3;
    background-color: var(--inner-bg-colour);
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    & h2 {
        margin-top: 0px;
        margin-bottom: 10px;
        text-align: center;
    }

    & p {
        margin-top: 10px;
        text-align: center;
    }
}

.reservation_form {
    background-color: white;
    height: 80%;
    color: black;
    padding: 20px 0px;
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;

    & fieldset {
        padding-top: 30px;
        height: 100%;
        display: flex;
        flex-direction: column;
        border: 0px;

        & legend {
            font-size: 24px;
            font-weight: bold;
        }

        & * {
            flex: auto;
            font-size: 24px;
            width: 100%;
            font-size: 20px;
            font-family: Louis;
            padding: 0px 10px;
            margin: 0px;
            box-sizing: border-box;
        }

        & button {
            background-color: var(--red);
        }
    }
}

.contact-container {
    grid-column: 3/4;
    grid-row: 1/2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: var(--text);
}

.contact-content {
    flex: auto;
    padding: 0px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.contact-item {
    display: flex;
    gap: 50px;
}

.small-icon {
    width: auto;
    height: 50px;
}

.menu-items-container {
    padding: var(--content-padding);
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: repeat(auto-fit, var(--card-width));
    gap: 25px;
}

.menu-card {
    background-color: white;
    width: var(--card-width);
    padding: 10px 20px;
    box-sizing: border-box;
    border-radius: var(--card-board-radius);
    box-shadow: 2px 2px 5px rgb(15, 15, 15);
    font-size: var(--card-text);

    & * {
        text-align: center;
    }
}

.menu-item-img {
    width: 100%;
    height: auto;
    border-radius: var(--card-board-radius);
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,mBAAmB;IACnB,cAAc;IACd,uBAAuB;IACvB,qBAAqB;;IAErB,4BAA4B;IAC5B,kCAAkC;IAClC,qBAAqB;IACrB,oBAAoB;IACpB,YAAY;;IAEZ,yBAAyB;IACzB,iBAAiB;IACjB,mBAAmB;IACnB,gDAAgD;AACpD;;AAEA;IACI,kBAAkB;IAClB,4CAAuC;AAC3C;;AAEA;IACI,mBAAmB;IACnB,4CAA0B;AAC9B;;AAEA;IACI,WAAW;IACX,kCAAkC;IAClC,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,kCAAkC;IAClC,gCAAgC;IAChC,4BAA4B;;IAE5B;QACI,kBAAkB;QAClB,UAAU;QACV,eAAe;QACf,WAAW;QACX,iCAAiC;IACrC;AACJ;;AAEA;IACI,cAAc;IACd,aAAa;IACb,oBAAoB;AACxB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,WAAW;IACX,+BAA+B;IAC/B,kBAAkB;IAClB;AACJ;;AAEA;IACI,YAAY;IACZ,qCAAqC;AACzC;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,MAAM;IACN,gBAAgB;IAChB,WAAW;IACX,WAAW;AACf;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI;QACI,YAAY;IAChB;AACJ;;AAEA;IACI;QACI,WAAW;IACf;AACJ;;AAEA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,6EAA6E;IAC7E,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,uBAAuB;AAC3B;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,cAAc;IACd,WAAW;IACX,kBAAkB;IAClB,kBAAkB;;;IAGlB;QACI,kBAAkB;IACtB;;IAEA;QACI,gBAAgB;QAChB,eAAe;IACnB;AACJ;;AAEA;IACI,kCAAkC;IAClC,iBAAiB;IACjB,oBAAoB;IACpB,+BAA+B;IAC/B,YAAY;AAChB;;AAEA;IACI,oBAAoB;;IAEpB,kBAAkB;IAClB,8BAA8B;IAC9B,uBAAuB;IACvB,kBAAkB;IAClB,6CAA6C;IAC7C,0BAA0B;IAC1B,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,2BAA2B;IAC3B,gBAAgB;IAChB,aAAa;AACjB;;AAEA;;;;IAII,wCAAwC;IACxC,sBAAsB;IACtB,aAAa;IACb,kBAAkB;;IAElB;QACI,8BAA8B;IAClC;AACJ;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,UAAU;IACV,oBAAoB;IACpB,oBAAoB;IACpB,aAAa;;IAEb;QACI,UAAU;IACd;AACJ;;AAEA;IACI,gBAAgB;IAChB,aAAa;AACjB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,wCAAwC;IACxC,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;;IAEvB;QACI,eAAe;QACf,mBAAmB;QACnB,kBAAkB;IACtB;;IAEA;QACI,gBAAgB;QAChB,kBAAkB;IACtB;AACJ;;AAEA;IACI,uBAAuB;IACvB,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,kBAAkB;;